<template>
    <div>
		<div class="vx-row">
			<div class="vx-col w-full mb-6">
				<vx-card>
					<h5 class="font-bold">{{user_data.name}}</h5>
					<div>
						<small>가입일 : {{user_data.register_day}}</small>
						<span class="badge">고객</span>
						<small class="mx-4">|</small>
						<span class="text-primary">스케줄 노트</span>
					</div>
				</vx-card>
			</div>
		</div>
		<div class="vx-row">
			<div class="vx-col w-full md:w-2/3 lg:w-2/3 xl:w-2/3 mb-base">
				<vx-card class="mb-6">
					<vs-tabs>
						<vs-tab label="상세정보">
							<div class="con-tab-ejemplo mb-5">
								<div class="vx-row pt-5">

									<!-- Avatar Col -->
									<div class="vx-col" id="avatar-col">
										<div class="img-container mb-4">
										<img :src="user_data.avatar" class="rounded w-full" />
										</div>
									</div>

									<!-- Information - Col 1 -->
									<div class="vx-col flex">
										<table>
										<tr>
											<td class="font-semibold pr-4">Status</td>
											<td>{{ user_data.status }}</td>
										</tr>
										<tr>
											<td class="font-semibold pr-4">Name</td>
											<td>{{ user_data.name }}</td>
										</tr>
										<tr>
											<td class="font-semibold pr-4">Email</td>
											<td>{{ user_data.email }}</td>
										</tr>
										<tr>
											<td class="font-semibold pr-4">RelationShip</td>
											<td>{{ user_data.relationship }}</td>
										</tr>
										</table>
									</div>
								</div>
							</div>
							
							<div class="vx-col mb-6 option_menu flex flex-wrap justify-between">
								<div class="flex">
									<flat-pickr :config="config" v-model="rangeData" placeholder="날짜 선택" />
									<vs-button class="ml-2" size="small">일지보기</vs-button>
								</div>
								<vs-button class="ml-2 new_note" @click="new_note=true">새 노트</vs-button>
								<vs-prompt
									class="position-relative"
									title="새 노트 작성"
									accept-text= "작성하기"
									cancel-text = "취소"
									button-cancel = "border"
									:active.sync="new_note">
									<div>
										<vs-textarea class="w-full" type="text" ref="customArea" v-model="new_log"></vs-textarea>
									</div>
								</vs-prompt>
							</div>
							
							<div class="p-3">
								<div v-for="(item, index) in note" :key="'n'+index" class="pb-5 border-bottom mb-3">
									<div class="flex py-2">
										<span class="date">{{item.date}}</span>
										<span class="badge">노트</span>
									</div>
									<p class="note">{{item.content}}</p>
								</div>
								
								<div v-for="(item, index) in reservation" :key="'r'+index" class="pb-5 border-bottom mb-3">
									<div class="flex items-center justify-between">
										<div class="flex py-2">
											<span class="date">{{item.date}}</span>
											<span class="badge">예약</span>
										</div>
										<div class="flex items-center">
											<span class="ml-3">{{timeAmPm(item.time)}}</span>
											<vs-button size="small" class="ml-3" v-if="!logWrite" @click="write_log=true">수정</vs-button>
										</div>
									</div>

									<div class="note" v-if="logWrite">
										<vs-button size="small" class="mr-2" type="border" @click="write_log=true"><i class="feather icon-pack icon-plus mr-2"></i>일지 쓰기</vs-button>
										<!-- <vs-textarea class="w-full"></vs-textarea> -->
										<vs-button size="small"><i class="feather icon-pack icon-upload-cloud mr-2"></i>일지 업로드</vs-button>
									</div>
									<div class="note" v-else>
										<div class="mb-3" style="min-height:42px;" v-for="(item,index) in log" :key="'log'+index">
											<small class="date-label">일지</small>
											<p v-html="item==''?`<span class='text_pale'>내용이 없습니다</span>`:item"></p>
										</div>
									</div>
									<vs-prompt
										class="position-relative"
										title="일지쓰기"
										accept-text= "작성하기"
										cancel-text = "취소"
										button-cancel = "border"
										:active.sync="write_log">
										<div>
											<div>
												<small class="date-label">일지</small>
												<vs-input class="w-full" type="text" v-model="log[0]"></vs-input>
											</div>
											<div>
												<small class="date-label">일지</small>
												<vs-input class="w-full" type="text" v-model="log[1]"></vs-input>
											</div>
											<div>
												<small class="date-label">일지</small>
												<vs-input class="w-full" type="text" v-model="log[2]"></vs-input>
											</div>
											<div>
												<small class="date-label">일지</small>
												<vs-input class="w-full" type="text" v-model="log[3]"></vs-input>
											</div>
										</div>
									</vs-prompt>
								</div>
							</div>
						</vs-tab>
						<vs-tab label="내역">
							<div class="con-tab-ejemplo">
								<div>
									<div class="py-5 flex option_menu">
										<flat-pickr :config="configBilling" v-model="date" placeholder="기간 선택" class="w-full" />
										<vs-button class="ml-2">새 내역 추가</vs-button>
									</div>
									
									<div v-for="(item, index) in billing" :key="'b'+index" class="billing_table pb-3 border-bottom mb-3">
										<div class="flex align-items-center justify-content-between py-2 tr">
											<div class="flex sm-w-full sm-column">
												<span class="ml-0 mr-5 badge date">{{item.date}}</span>
												<div class="flex-column flex">
													<div>
														<b>{{item.type}}</b>
														<span class="badge bg-danger text-white" v-if="item.status=='unpaid'">UNPAID</span>
													</div>
													<div>{{item.note}}</div>
												</div>
											</div>
											<span class="price">$ {{item.price}}
												<vs-button size="small" v-if="item.status=='unpaid'" class="ml-4 payBtn">결제 등록</vs-button>
											</span>
										</div>
									</div>
								</div>
							</div>
						</vs-tab>
					</vs-tabs>
					<!-- Avatar -->
				</vx-card>
			</div>
			<div class="vx-col w-full md:w-1/3 lg:w-1/3 xl:w-1/3 mb-base">
				<vx-card class="mb-6">
					<vs-table :data="dc_list" class="w-full">
						<template slot-scope="{data}">
							<vs-tr :key="indextr" v-for="(tr, indextr) in data" >
								<vs-td :data="data[indextr].title">{{ data[indextr].title }}
								</vs-td>
							</vs-tr>
						</template>
					</vs-table>
					<vs-button class="mt-3" v-if="dc_list&&dc_list.length>0" @click="$router.push('/send-document')">서류 보내기</vs-button>
				</vx-card>
				<vx-card>
					<h4 class="mb-5">Contact</h4>
					<div class="w-full contact">
						<div class="flex">
							<label for="" class="mr-2 text-primary">
								Phone
								<i class="feather icon-phone text-primary mr-3"></i>
							</label>
							<p>{{user_data.phone}}</p>
						</div>
						<div class="flex">
							<label for="" class="mr-2 text-primary">
								Email
								<i class="feather icon-mail text-primary"></i>
							</label>
							<p>{{user_data.email}}</p>
						</div>
						</div>
				</vx-card>
			</div>
		</div>
	</div>
</template>
<script>
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
export default {
	components:{
		flatPickr,
	},
	data(){
		return{
			date:null,
			write_log:false,
			new_note:false,
			log:['','','',''],
			new_log:'',
			rangeData:null,
			configBilling:{
				mode:"range",
				locale: { 
					rangeSeparator: ' 부터 ' 
				},
				dateFormat: "Y-m-d",
			},
            config: {
				mode:"range",
				locale: { 
					rangeSeparator: ' 부터 ' 
				},
				dateFormat: "Y-m-d",
            },
			user_data: {
				avatar:'https://image.flaticon.com/icons/png/512/3135/3135715.png',
				id:0,
				email:'aimbot@naver.com',
				name:'김가명',
				relationship:'client',
				status: '활성화',
				phone:'010-1234-5678',
				register_day:'2020-05-21'
			},
			dc_list:[
				{
					title:'서류 이름'
				}
			],
			note:[
				{
					date:'2021-07-01',
					content:'VIP 고객님으로 가입 3년동안 누적 예약수 18회.'
				}
			],
			reservation:[
				{
					date:'2021-07-01',
					time:'16:10',
					content:'VIP 고객님으로 가입 3년동안 누적 예약수 18회.'
				}
			],
			billing:[
				{
					date:'2021-07-01',
					type:'invoice',
					status:'unpaid',
					price:100,
					note:'결제 내역 관련'
				},
				{
					date:'2021-07-01',
					type:'invoice',
					status:'paid',
					price:100,
					note:'결제 내역 관련'
				},
				{
					date:'2021-07-01',
					type:'invoice',
					status:'paid',
					price:100,
					note:'결제 내역 관련'
				},
			]

		}
	},
	mounted(){
		
	},
	methods:{
		timeAmPm(item){
			var hour = item.slice(0,2);
			var minute = item.slice(3,5);

			if(parseInt(hour)<12){
				return '오전 '+item
			}
			else if(parseInt(hour)==12){
				return '오후 '+item
			}
			else return '오후 ' + (hour-12) + ':' + minute
		}
	},
	computed:{
		logWrite(){
			const isEmpty = (currentValue) => currentValue == '';
			var c = this.log.every(isEmpty);
			return c
		},
	}
}
</script>
<style lang="scss" scoped>
.vs-con-textarea {
	margin-bottom: 0;
}
.option_menu input{
	overflow-x: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.option_menu button{
	flex-shrink: 0;
}
#avatar-col {
  width: 10rem;
}

#page-user-view {
  table {
    td {
      vertical-align: top;
      min-width: 140px;
      padding-bottom: .8rem;
      word-break: break-all;
    }

    &:not(.permissions-table) {
      td {
        @media screen and (max-width:370px) {
          display: block;
        }
      }
    }
  }
}

.contact{
	label{
		width: 30%;
	}
	p{
		width: 70%;
	}
}

@media screen and (min-width:1201px) and (max-width:1211px),
only screen and (min-width:636px) and (max-width:991px) {
  #account-info-col-1 {
    width: calc(100% - 12rem) !important;
  }
}
.date{
	font-weight: 600;
}
@media screen and (max-width:900px){
	.billing_table .tr{
		position: relative;
		flex-wrap: wrap;
	}
	.sm-w-full, .price{
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.sm-column{
		flex-direction: column;
    	align-items: start;
	}
	.sm-column>*{
		margin-bottom: 5px;
		text-align: left;
	}
	.new_note{
		margin-left: 0 !important;
		margin-top: 10px;
	}
}
</style>